* {
    font-family: Arial, sans-serif;
}

body {
    padding: 60px;
}

input {
    width: 100%;
    font-size: 1.2rem;
    color: gray;
}

.card-grid {
    display: flex;
    flex-wrap: wrap;
}

.card {
    overflow: hidden;
    align-content: center;
    border:1px solid gray;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img {
    max-height: 170px;
}
